.social-media-div {
    font-size: 2em;
  }
  
  .icon-button i {
    color: white;
  }
  .icon-button {
    margin-bottom: 10px;
  }
  
  .icon-button i {
    border-radius: 2.6rem;
    cursor: pointer;
    display: inline-block;
    font-size: 1.3rem;
    height: 2.6rem;
    line-height: 2.6rem;
    margin: 0 5px;
    position: relative;
    text-align: center;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    width: 2.6rem;
    margin-bottom: 10px;
  }
  
